import React, { useEffect, useState } from "react";
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";
import api from "../services/api";
import { Task } from "../types"; // Importao do tipo



const KanbanPage: React.FC = () => {
  const [tasks, setTasks] = useState<Task[]>([]);
  const [newTaskTitle, setNewTaskTitle] = useState(""); // Para o ttulo da nova tarefa
  const [taskStatus, setTaskStatus] = useState<"to-do" | "in-progress" | "done">("to-do"); // Status da tarefa
  const [dueDate, setDueDate] = useState<string>(""); // Para o campo de dueDate

  useEffect(() => {
    const fetchTasks = async () => {
      try {
        // Verificando o token e o userId no frontend
        const token = localStorage.getItem("token");
        console.log("Token enviado para o backend:", token);

  
        const response = await api.get("api/tasks");
        console.log("Tarefas recebidas:", response.data);  // Adicione um log aqui
        setTasks(response.data);
      } catch (error) {
        console.error("Erro ao buscar tarefas", error);
      }
    };
  
    fetchTasks();
  }, []);

  // Funo para adicionar uma nova tarefa
  const addTask = async () => {
    if (newTaskTitle.trim() && dueDate) {
      const token = localStorage.getItem("token");  // Obter o token do localStorage
      if (!token) {
        alert("Voc precisa estar logado para criar uma tarefa.");
        return;
      }

      try {
        const newTask = {
          title: newTaskTitle,
          status: taskStatus,
          dueDate: dueDate, // Incluindo o dueDate
        };
        const response = await api.post(
          "api/tasks",
          newTask,
          {
            headers: {
              "Authorization": `Bearer ${token}`,  // Enviar o token no cabealho
            },
          }
        );
        setTasks([...tasks, response.data]);
        setNewTaskTitle(""); // Limpa o campo aps adicionar
        setDueDate(""); // Limpa o campo de data
      } catch (error) {
        console.error("Erro ao adicionar tarefa", error);
      }
    } else {
      alert("Por favor, insira um ttulo e uma data para a tarefa.");
    }
  };

  // Funo para mover as tarefas entre os status
  const handleDragEnd = async (result: any) => {
    const { destination, source } = result;

    if (!destination) return; // Se o destino for nulo, no faa nada

    // Se a tarefa for movida para uma nova coluna ou nova posio
    if (destination.index !== source.index || destination.droppableId !== source.droppableId) {
      const taskId = tasks[source.index]._id;
      const newStatus = destination.droppableId as "to-do" | "in-progress" | "done";

      try {
        const token = localStorage.getItem("token");  // Obter o token do localStorage
        if (!token) {
          alert("Voc precisa estar logado para mover a tarefa.");
          return;
        }

        // Atualiza o status da tarefa no backend
        await api.put(
          `api/tasks/${taskId}`,
          { status: newStatus },
          {
            headers: {
              "Authorization": `Bearer ${token}`,  // Enviar o token no cabealho
            },
          }
        );

        // Atualiza a lista de tarefas localmente
        const updatedTasks = [...tasks];
        const [removed] = updatedTasks.splice(source.index, 1); // Remove a tarefa da posio original
        removed.status = newStatus; // Modifica o status da tarefa
        updatedTasks.splice(destination.index, 0, removed); // Insere a tarefa no novo local

        setTasks(updatedTasks);
      } catch (error) {
        console.error("Erro ao mover tarefa", error);
      }
    }
  };

  return (
    <div style={{ padding: "20px" }}>
      <h2>Kanban</h2>

      {/* Formulrio para adicionar nova tarefa */}
      <div style={{ marginBottom: "20px" }}>
        <input
          type="text"
          placeholder="Nova tarefa"
          value={newTaskTitle}
          onChange={(e) => setNewTaskTitle(e.target.value)}
          style={{
            padding: "10px",
            marginRight: "10px",
            borderRadius: "5px",
            border: "1px solid #ddd",
            width: "250px",
          }}
        />
        <select
          value={taskStatus}
          onChange={(e) => setTaskStatus(e.target.value as "to-do" | "in-progress" | "done")}
          style={{
            padding: "10px",
            marginRight: "10px",
            borderRadius: "5px",
            border: "1px solid #ddd",
          }}
        >
          <option value="to-do">A Fazer</option>
          <option value="in-progress">Em Andamento</option>
          <option value="done">Concludas</option>
        </select>

        {/* Campo de data para a tarefa */}
        <input
          type="date"
          value={dueDate}
          onChange={(e) => setDueDate(e.target.value)}
          style={{
            padding: "10px",
            marginRight: "10px",
            borderRadius: "5px",
            border: "1px solid #ddd",
          }}
        />

        <button onClick={addTask} style={{ padding: "10px 20px", cursor: "pointer", borderRadius: "5px" }}>
          Adicionar Tarefa
        </button>
      </div>

      {/* Kanban Board */}
      <DragDropContext onDragEnd={handleDragEnd}>
        <div style={{ display: "flex", justifyContent: "space-between" }}>
          {["to-do", "in-progress", "done"].map((status) => (
            <Droppable key={status} droppableId={status}>
              {(provided, snapshot) => (
                <div
                  ref={provided.innerRef}
                  {...provided.droppableProps}
                  style={{
                    width: "30%",
                    padding: "10px",
                    backgroundColor: snapshot.isDraggingOver ? "#e3e3e3" : "#f4f4f4",
                    borderRadius: "5px",
                  }}
                >
                  <h3>
                    {status === "to-do"
                      ? "A Fazer"
                      : status === "in-progress"
                      ? "Em Andamento"
                      : "Concludas"}
                  </h3>
                  {tasks
                    .filter((task) => task.status === status)
                    .map((task, index) => (
                      <Draggable key={task._id} draggableId={task._id} index={index}>
                        {(provided) => (
                          <div
                            ref={provided.innerRef}
                            {...provided.draggableProps}
                            {...provided.dragHandleProps}
                            style={{
                              ...provided.draggableProps.style,
                              padding: "10px",
                              margin: "10px 0",
                              backgroundColor: "#fff",
                              border: "1px solid #ddd",
                              borderRadius: "5px",
                            }}
                          >
                            {task.title}
                          </div>
                        )}
                      </Draggable>
                    ))}
                  {provided.placeholder}
                </div>
              )}
            </Droppable>
          ))}
        </div>
      </DragDropContext>
    </div>
  );
};

export default KanbanPage;
